import React from "react";

const NavigationDots = ({ active }) => {
  const navLinks = ["home", "about", "projects", "skills", "contact"];

  return (
    <div className="app__navigation">
      {navLinks.map((item) => (
        <a
          className={
            active === item
              ? "app__navigation-dot-active"
              : "app__navigation-dot"
          }
          href={`#${item}`}
          key={item}
        />
      ))}
    </div>
  );
};

export default NavigationDots;
