import { useState } from "react";
import { motion } from "framer-motion";
import ProjectCard from "../../components/projects/ProjectCard";
import ProjectFilters from "../../components/projects/ProjectFilters";
import { images } from "../../constants";
import "./Projects.scss";

const Projects = () => {
  const filters = ["All", "Android", "iOS", "React", "Node"];
  const projects = [
    {
      title: "Holiday Hype",
      description: `It is an android app which helps travelers easily plan 
        and book their complete trip with the widest selection of flights, 
        hotels, attractions and restaurants.`,
      gitHubUrl: "https://github.com/kamalpreetsinghh/HolidayHype",
      tag: "Android",
      imageURL: images.holiday_hype,
    },
  ];

  const [filteredProjects, setFilteredProjects] = useState(projects);
  const [activeFilter, setActiveFilter] = useState("All");
  const [animateCard, setAnimateCard] = useState({ y: 0, opacity: 1 });

  const handleProjectFilter = (item) => {
    setActiveFilter(item);

    setAnimateCard([{ y: 0, opacity: 1 }]);

    if (item === "All") {
      setFilteredProjects(projects);
    } else {
      setFilteredProjects(projects.filter((project) => project.tag === item));
    }
  };

  return (
    <section id="projects">
      <h2 className="head-text app__heading">
        My <span>Projects</span>
      </h2>
      <ProjectFilters
        filters={filters}
        activeFilter={activeFilter}
        onFilterChange={handleProjectFilter}
      />
      <motion.div
        animate={animateCard}
        transition={{ duration: 0.5, delayChildren: 0.5 }}
        className="app__projects"
      >
        {filteredProjects.map((project) => (
          <ProjectCard project={project} key={project.title} />
        ))}
      </motion.div>
    </section>
  );
};

export default Projects;
