import React from "react";

const NavLinks = () => {
  const navLinks = ["home", "about", "projects", "skills", "contact"];

  return (
    <ul className="app__navbar-links">
      {navLinks.map((item) => (
        <li className="app__flex p-text" key={`link-${item}`}>
          <a href={`#${item}`}>{item}</a>
          <div />
        </li>
      ))}
    </ul>
  );
};

export default NavLinks;
